<template>
  <div class="container">
    <Divider orientation="right"></Divider>
    <Form label-position="top">
      <div id="form_lable">
        <label for></label>
        <FormItem>
          <Input v-model="formData.title"></Input>
        </FormItem>
        <label for>Mavon-EditorMarkdown</label>
        <!-- markdown -->
        <mavon-editor
          class="markdown"
          ref="md"
          @imgAdd="$imgAdd"
          @imgDel="$imgDel"
          :toolbars="markdownOption"
          v-model="formData.content"
        />

        <label for></label>
        <FormItem>
          <Input
            type="textarea"
            :autosize="{minRows: 2,maxRows: 5}"
            placeholder="Enter something..."
            v-model="formData.summary"
          ></Input>
        </FormItem>
        <label for></label>
      </div>
      <FormItem prop="interest">
        <CheckboxGroup v-model="formData.categories">
          <Checkbox label="JavaScript"></Checkbox>
          <Checkbox label="Vue"></Checkbox>
          <Checkbox label="React"></Checkbox>
          <Checkbox label="CSS"></Checkbox>
          <Checkbox label="HTML"></Checkbox>
          <Checkbox label="Andriod"></Checkbox>
          <Checkbox label="JAVA"></Checkbox>
          <Checkbox label=""></Checkbox>
          <Input type="text" placeholder="" v-model="formData.category_add"></Input>
        </CheckboxGroup>
      </FormItem>
      <!--  -->
      <FormItem>
        <Button id="btn1" type="primary" @click="handleSubmit('formData')"></Button>
        <Button id="btn2"  @click="handleReset" style="margin-left: 8px"></Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // 
      formData: {
        summary: "",
        title: "",
        content: "",
        categories: [],
        category_add: ""
      },
      img_file: {},
      markdownOption: {
        fontSize: "14px",
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        // fullscreen: true, // 
        readmodel: true, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: true, // eventssave
        /* 1.4.2 */
        navigation: true, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: true // 
      }
    };
  },
  created() {
      console.log(this.$route);
      this.axios
      .get("/blogs?id=" + this.$route.query.id)
      .then(response => {
        this.formData = response.data;
        this.formData.categories = [response.data.category];
      });
  },
  methods: {
    // 
    // 
    $imgAdd(pos, $file) {
      // 
      this.img_file[pos] = $file;
    },
    // 
    $imgDel(pos) {
      delete this.img_file[pos];
    },

    // 
    handleSubmit(name) {
      // ..
      var formdata = new FormData();
      for (var _img in this.img_file) {
        formdata.append(_img, this.img_file[_img]);
      }

      // 
      if (formdata) {
        this.axios({
          url: "/upload/img",
          method: "post",
          data: formdata,
          headers: {
            "Content-Type": "multipart/form-data",
            Authorization: localStorage.getItem("token")
          }
        }).then(res => {
          if (res.status == 200) {
            var url_list = res.data.urls;
            console.log(url_list);
            /**
             *  res = [[pos, url], [pos, url]...]
             * pos 0
             * url url
             */
            // .url![...](0) -> ![...](url)
            for (var i=0; i<url_list.length; i++) {
              // $vm.$img2Url 
              // console.log(url_list[i]);
              this.$refs.md.$img2Url(parseInt(url_list[i].key), url_list[i].url);
            }
            // 
            // 
            if (this.formData.category_add)
              this.formData.categories.push(this.formData.category_add);
            //
            console.log("send");  
            this.axios({
              method: "post",
              url: "/blogs/edit",
              data: this.formData,
              headers: {
                Authorization: localStorage.getItem("token")
              }
            }).then(response => {
              console.log(response);
              this.$Notice.success({
                    title: ''
                });
            });
          } else {
            this.$Notice.error({
              title: "",
              desc: ""
            });
          }
        });
      }
    },

    handleReset() {
      this.formData = {
        summary: "",
        title: "",
        content: "",
        categories: [],
        category_add: ""
      };
    }
  }
};
</script>


<style scoped>


#tip {
  text-align: left;
  margin: 0 0 10px;
}

#form_lable label {
  padding-left: 20px;
  text-align: left;
  display: block;
  margin-left: 0;
  margin-bottom: 5px;
  /* float: left; */
  border-left: 4px solid rgb(175, 50, 60);
}
#form_lable .markdown {
  border-radius: 5px;
  margin-bottom: 20px;
  height: 500px;
  max-height: 500px;
}
#form_lable .v-note-wrapper {
  z-index: 0;
}
.container {
  max-width: 90%;
}

</style>



